import mIcon from "../../components/m-icon/m-icon";
import {getIndex, donate} from "../../api/api.js";

export default {
	data() {
		return {
			propInfo: '',
			isHidden: this.$store.state.isHidden,
			changColor: '#EFEFF4',
			data1: '+0.91%',
			data2: '0.082',
			data3: '24H量 89，893，708',
			noBind: '暫未綁定',
			Bind: '已綁定',
			panVlaue: '198,878',
			donateValue: '10'
		}
	},
	components:{
		mIcon
	},
	methods: {
		assetHidden() {
			if(this.isHidden == false) {
				this.changColor = '#9B9B9B';
				this.isHidden = true;
				uni.setStorageSync('ASSET_KEY', this.isHidden);
			} else {
				this.changColor = '#EFEFF4';
				this.isHidden = false;
				uni.setStorageSync('ASSET_KEY', this.isHidden);
			}
		},
		getMyIndex() {
			getIndex().then(data => {
				this.propInfo = data;
			})
		},
		add() {
			this.donateValue++;
		},
		sub() {
			this.donateValue--;
		},
		donatePan() {
			donate(this.donateValue).then(data=> {
				this.getMyIndex();
			});
		}
	},
	onLoad() {
		this.getMyIndex();
	}
}
